.wrapper {
  position: absolute;
  bottom: 0;
  width: 100vw;
  height: 300px;
  overflow-x: scroll;
  pointer-events: none;
}

.colorControllerPanel {
  height: 110px;
  width: 100vw;
  position: absolute;
  bottom: 0;
  left: 0;
  background: #aaa;
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
  pointer-events: auto;
}

.colorControllerPanel::-webkit-scrollbar {
  display: none
}

.colorControllerPanel.isCollapsed {
  bottom: -70px;
}

.toggleCollapseButton {
  width: 100%;
  height: 5px;
  background: #fff;
  cursor: ns-resize;
  display: none;
}

.colorControllers {
  width: 1500px;
  /*height: calc(100% - 5px);*/
  height: 100%;
  white-space: nowrap;
}

.colorControllerContainer {
  display: inline-block;
  height: 100%;
  width: 310px;
  min-width: 20vw;
  /*border: 1px solid #fff;*/
}




