.customSelect :global(.Select-control) {
  height: 100% !important;
  border: none !important;
}

.customSelect :global(.Select-control:hover) {
  -webkit-box-shadow: none !important;
          box-shadow: none !important;
} 

.customSelect :global(.Select-value-label) {
  padding: 0 !important;
}

.customSelect :global(.Select-option) {
  padding: 4px 6px !important;
}

.lightText {
  color: #fff !important;
  background-color: rgba(255,255,255,0) !important;
}

.lightText:global(.is-focused) {
    color: #fff !important;
    background-color: rgba(255,255,255,0.1) !important;
}

.lightText:global(.is-selected) {
  color: #fff !important;
  background-color: rgba(255,255,255,0.2) !important;
}

.darkText {
  color: #242424 !important;
  background-color: rgba(255,255,255,0) !important;
}

.darkText:global(.is-focused) {
    color: #242424 !important;
    background-color: rgba(0,0,0,0.05) !important;
}

.darkText:global(.is-selected) {
  color: #242424 !important;
  background-color: rgba(0,0,0,0.1) !important;
}

.customSelect :global(.Select-control .Select-value) {
  padding-left: 0 !important;
  padding-right: 0 !important;
}



